"use client";

import React, { useState } from "react";
import { Pencil, Trash2 } from "lucide-react";

export default function ProductsPage() {
  const [products, setProducts] = useState([
    { id: 1, name: "Bandhani" },
    { id: 2, name: "Galaxy S1" },
    { id: 3, name: "Galaxy S2" },
    { id: 4, name: "Lenovo Ideapad" },
  ]);

  const [selected, setSelected] = useState([]);
  const [search, setSearch] = useState("");

  const [showAddForm, setShowAddForm] = useState(false);
  const [newProduct, setNewProduct] = useState("");

  const [editingId, setEditingId] = useState(null);
  const [editedName, setEditedName] = useState("");

  const filtered = products.filter((p) =>
    p.name.toLowerCase().includes(search.toLowerCase())
  );

  const handleAddProduct = () => {
    if (!newProduct.trim()) {
      alert("Please enter product name");
      return;
    }

    const newItem = {
      id: Date.now(),
      name: newProduct.trim(),
    };

    setProducts([...products, newItem]);
    setNewProduct("");
    setShowAddForm(false);
  };

  const handleSelectRow = (id) => {
    setSelected((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]
    );
  };

  const handleSelectAll = (e) => {
    if (selected.length === filtered.length) {
      setSelected([]);
    } else {
      setSelected(filtered.map((p) => p.id));
    }
  };

  const handleDelete = (id) => {
    setProducts(products.filter((p) => p.id !== id));
    setSelected(selected.filter((sid) => sid !== id));
  };

  const handleBulkDelete = () => {
    setProducts(products.filter((p) => !selected.includes(p.id)));
    setSelected([]);
  };

  return (
    <div className="bg-[#f9f9f9] min-h-screen flex justify-center py-8">
      <div className="bg-white border border-[#d9d9d9] w-[95%] md:w-[90%] p-6 rounded-lg shadow-sm">

        {/* Header */}
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold text-gray-700">Products</h2>

          <button
            onClick={() => setShowAddForm(!showAddForm)}
            className="bg-[#1b2b41] hover:bg-[#142336] text-white text-sm font-medium px-4 py-2 rounded"
          >
            {showAddForm ? "Close" : "Add Product"}
          </button>
        </div>

        {/* ‚≠ê Add Form (Same as Categories Page) */}
        {showAddForm && (
          <div className="border border-gray-300 p-5 rounded mb-6 bg-[#f7f9fb]">
            <h3 className="text-lg font-semibold mb-3 text-gray-800">
              Add New Product
            </h3>

            <label className="block mb-2 text-sm text-gray-700">
              Product Name
            </label>

            <input
              type="text"
              value={newProduct}
              onChange={(e) => setNewProduct(e.target.value)}
              className="w-full border border-gray-300 px-3 py-2 rounded text-black mb-2"
              placeholder="Enter product name"
            />

            <div className="flex justify-end gap-3 mt-3">
              <button
                className="bg-gray-300 hover:bg-gray-400 px-5 py-2 rounded"
                onClick={() => setShowAddForm(false)}
              >
                Cancel
              </button>

              <button
                className="bg-[#00a7cf] hover:bg-[#0094b8] text-white px-5 py-2 rounded"
                onClick={handleAddProduct}
              >
                Save
              </button>
            </div>
          </div>
        )}

        {/* Search */}
        <div className="flex justify-end gap-2 mb-4">
          <input
            type="text"
            placeholder="Product Name"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="border text-black border-[#d9d9d9] rounded-sm px-3 py-2 text-sm"
          />
        </div>

        {/* Table */}
        <div className="overflow-x-auto border border-[#d9d9d9] rounded-md">
          <table className="w-full border-collapse text-sm">
            <thead className="bg-[#f1f1f1] text-gray-800 font-semibold">
              <tr>
                <th className="border p-2 w-12">
                  <input
                    type="checkbox"
                    checked={
                      selected.length === filtered.length &&
                      filtered.length > 0
                    }
                    onChange={handleSelectAll}
                  />
                </th>

                <th className="border p-2">SR. NO.</th>
                <th className="border p-2">PRODUCT NAME</th>
                <th className="border p-2 text-center">EDIT</th>
                <th className="border p-2 text-center">DELETE</th>
                <th className="border p-2 text-center">VIEW LEADS</th>
              </tr>
            </thead>

            <tbody>
              {filtered.map((p, index) => (
                <tr key={p.id} className="hover:bg-gray-50 text-gray-700">

                  <td className="border p-2">
                    <input
                      type="checkbox"
                      checked={selected.includes(p.id)}
                      onChange={() => handleSelectRow(p.id)}
                    />
                  </td>

                  <td className="border p-2">{index + 1}</td>

                  <td className="border p-2">
                    {editingId === p.id ? (
                      <input
                        className="border px-2 py-1 w-full rounded"
                        value={editedName}
                        onChange={(e) => setEditedName(e.target.value)}
                      />
                    ) : (
                      p.name
                    )}
                  </td>

                  <td className="border p-2 text-center">
                    {editingId === p.id ? (
                      <>
                        <button
                          className="text-blue-600 font-semibold mr-2"
                          onClick={() => {
                            setProducts((prev) =>
                              prev.map((item) =>
                                item.id === p.id
                                  ? { ...item, name: editedName }
                                  : item
                              )
                            );
                            setEditingId(null);
                          }}
                        >
                          Update
                        </button>

                        <button
                          className="text-red-600 font-semibold"
                          onClick={() => setEditingId(null)}
                        >
                          Cancel
                        </button>
                      </>
                    ) : (
                      <button
                        className="text-gray-700 hover:text-blue-600"
                        onClick={() => {
                          setEditingId(p.id);
                          setEditedName(p.name);
                        }}
                      >
                        <Pencil size={16} />
                      </button>
                    )}
                  </td>

                  <td className="border p-2 text-center">
                    <button
                      onClick={() => handleDelete(p.id)}
                      className="text-gray-700 hover:text-red-600"
                    >
                      <Trash2 size={16} />
                    </button>
                  </td>

                  <td className="border p-2 text-center">
                    <button className="bg-red-600 hover:bg-red-700 text-white px-4 py-1 rounded text-sm">
                      View Leads
                    </button>
                  </td>
                </tr>
              ))}

              <tr>
                <td colSpan="6" className="p-4 text-left">
                  <button
                    onClick={handleBulkDelete}
                    className="bg-red-600 hover:bg-red-700 text-white px-8 py-2 rounded-sm"
                  >
                    Delete ({selected.length})
                  </button>
                </td>
              </tr>
            </tbody>

          </table>
        </div>
      </div>
    </div>
  );
}
